}
blockquote::before, blockquote::after {
  font-size: 6rem;
  opacity: 0.5;
  color: #898989;
  position: absolute;
  clear: both;
  display: block;
}
blockquote::before {
  content: "“";
  top: -5px;
}
blockquote::after {
  content: "”";
  right: 10px;
  bottom: -30px;
}
blockquote p {
  word-wrap: break-word;
}

table {
  margin: 0 40px 40px;
  background: url(../img/table_bg.jpg);
  box-sizing: border-box;
  font-size: 1.8rem;
  box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.5);
}
@media screen and (max-width: 736px) {
