}
table tr th, table tr td {
  padding: 7px 12px;
  border-right: 1px solid #c8c8c8;
  vertical-align: middle;
  text-align: left;
  background: url(../img/widget_h_line.png) repeat-x;
  font-size: 1.6rem;
}
table tr th:last-child, table tr td:last-child {
  border: none;
}
table tr th {
  font-weight: bold;
  background-color: rgba(207, 207, 207, 0.5);
}
table tr td::before, table tr td::after {
  content: "";
  height: 15px;
  border: none;
  display: table-cell;
}
@media screen and (max-width: 736px) {
  table {
    width: 95%;
    margin: 0 auto 40px;
    box-sizing: border-box;
    display: block;
    clear: both;
  }
  table thead {
    display: block;
    float: left;
    overflow-x: scroll;
  }
  table tbody {
    display: block;
    width: auto;
    overflow-x: auto;
    white-space: nowrap;
  }
  table tr {
    display: inline-block;
    margin: 0 -3px;
  }
  table tr th, table tr td {
    padding: 20px;
  }
  table tr th:last-child, table tr td:last-child {
    border-right: 1px solid #c8c8c8;
  }
  table tr th {
    box-sizing: border-box;
    display: block;
  }
  table tr td {
    display: block;
  }
  table tr td:first-child {
    text-align: left;
  }
  table tr td::before, table tr td::after {
    height: auto;
  }
