.hcb_wrap pre::before {
  background-color: #dedede;
}

.hcb_wrap pre::before {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  box-sizing: border-box;
  min-width: 4em;
  padding: 0 10px;
  font-size: 12px;
  font-family: "Source Sans Pro", sans-serif;
  line-height: 20px;
  text-align: center;
  content: attr(data-lang);
}

.hcb_wrap pre.prism, .hcb_wrap pre.prism code {
  color: #1f1e1e;
  text-shadow: 0 1px #fff;
  background: #f7f6f6;
}

.hcb_wrap pre.line-numbers {
  position: static;
  padding-left: 3.75em;
  counter-reset: linenumber;
}

.hcb_wrap .line-numbers .line-numbers-rows {
  position: absolute;
  top: -2px;
  left: -3.75em;
  width: 3em;
  font-size: inherit;
  letter-spacing: -1px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
}

.line-numbers .line-numbers-rows {
  border-right: 1px solid #999;
}

.hcb_wrap code, .hcb_wrap span {
  box-sizing: border-box;
  font-size: inherit;
  font-family: "Source Sans Pro", sans-serif;
  line-height: 1.5;
}

.hcb_wrap pre.prism {
  overflow-x: scroll;
  white-space: pre;
}

